  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/
*,
*::before,
*::after {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
}
* {
  margin: 0;
  font-family: "Patrick Hand SC", cursive;
  font-weight: 400;
  font-style: normal;
}
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}
input,
button,
textarea,
select {
  font: inherit;
}
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}
#root,
#__next {
  isolation: isolate;
}

.cont {
  display: grid;
  height: 100%;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(6, 1fr);
  grid-auto-columns: 1fr;
  grid-auto-rows: 1fr;
  justify-items: center;
  align-items: center;
}
h1 {
  grid-column: 1/3;
  font-size: 50px;
  font-family: "Permanent Marker", cursive;
  font-weight: 400;
  font-style: normal;
  text-shadow: -3px 3px 0px black;
  color: whitesmoke;
}
form {
  grid-row: 1;
  grid-column: 4/6;
  display: flex;
  width: 50%;
}
.sun {
  height: 180px;
  width: 400px;
  grid-row: 2/4;
  grid-column: 1/3;
  color: whitesmoke;

  /* box-shadow:
    rgb(149, 156, 161) 3px 3px 6px 0px inset,
    rgba(255, 255, 255, 0.5) -3px -3px 6px 1px inset; */
  border-radius: 200px;
}
.curr {
  height: 180px;
  width: 890px;
  grid-row: 2/4;
  grid-column: 3 / 7;

  color: white;
  /* box-shadow:
    rgba(17, 17, 26, 0.1) 0px 1px 0px,
    rgba(17, 17, 26, 0.1) 0px 8px 24px,
    rgba(17, 17, 26, 0.1) 0px 16px 48px; */
  /* border-radius: 15px; */
  border-left: solid whitesmoke 1px;

  display: flex;
  align-items: end;
  justify-items: center;
}
.week {
  height: 300px;
  width: 98%;
  grid-row: 4/-1;
  grid-column: 1/-1;

  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: space-evenly;
  gap: 40px;
}
.week > div {
  box-shadow:
    rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset,
    rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
  height: 100px;
  width: 30%;
  border-radius: 50px;
  color: white;

  display: flex;
}
.search {
  height: 25px;
  width: 25px;
  fill: whitesmoke;
}
.search:hover {
  cursor: pointer;
}
button {
  border: none;
  background: none;
}
.icon-box {
  height: 100%;
  flex: 0.5;

  display: flex;
  align-items: center;
  justify-content: center;
}
.curr-icon {
  height: 60%;
}
.curr > p {
  height: 50%;
  flex: 1.5;

  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.6rem;
}
.temp-box {
  height: 100%;
  flex: 0.5;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.temp {
  font-size: 2.8rem;
}
.feelslike {
  font-size: 1.5rem;
}
.weekday > div {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
}
.weekday > div > img {
  width: 40%;
}
.sun,
.sun > div {
  display: flex;
  align-items: center;
  justify-content: center;
}
.sun > div {
  flex: 1;
  flex-direction: column;
}
#search {
  appearance: none;
  border: none;
  padding: 5px;
  width: 100%;
  border-bottom: solid 2px whitesmoke;
  font-size: 20px;
  background-color: #403f4c;
  color: whitesmoke;

  transition: all ease-in-out 400ms;
}
#search:focus {
  outline: none;
}
#search:focus:valid {
  border-bottom: solid 2px rgb(37, 202, 37);
}
#search:focus:invalid {
  border-bottom: solid 2px rgb(214, 40, 40);
}
#search::-webkit-search-cancel-button {
  -webkit-appearance: none;
}
body {
  background-color: #403f4c;
}
.sunrise > .img,
.sunset > .img {
  fill: whitesmoke;
  width: 45%;
}
.sunset > p,
.sunrise > p {
  font-size: 1.3rem;
}
.loading {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  transition: 200ms ease-in-out;
  background-color: #403f4c;
  border-radius: 50%;
  z-index: 10;
  width: 130px;
  height: 130px;

  display: flex;
  align-items: center;
  justify-content: center;
}
.loading.active {
  animation: loading 2s linear infinite;
}
@keyframes loading {
  0% {
    transform: translate(-50%, -50%) scale(1) rotate(0);
  }
  100% {
    transform: translate(-50%, -50%) scale(1) rotate(-360deg);
  }
}
.overlay {
  position: fixed;
  opacity: 0;
  transition: 200ms ease-in-out;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  pointer-events: none;
}
.overlay.active {
  pointer-events: all;
  opacity: 1;
}
